html {
  font-family: "Open Sans", sans-serif; /* this should be the rest of the output you got from Google Fonts */
  background-color: #181818;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: white; /*This is the only way I can get Archives to be white*/

  font-size: 30px;
}

.body {
}

.text {
  margin: 0;
  font-size: 21px;
  align-items: stretch;
  color: white;
}

.container {
  /*margin: 1rem;*/
}

.nav {
  background-color: #000000;
  color: white;
  display: flex;
  align-items: stretch;
  gap: 30%;
  /*border: solid;
  border: #000000;
  border: 300px;*/
}

.site-title {
  /*font-size: 2rem;*/
  font-size: 2.3vw;
  border-left: 0px solid;
  border-right: 0px solid;
}

.nav ul {
  /*font-size: 2rem;*/
  font-size: 2vw;
  justify-content: space-evenly;
  padding-left: 30px;
  margin: 10px;
  list-style: none;
  display: flex;
  gap: 10%;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0.2rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border-left: 3px solid;
  border-right: 3px solid;
}

.nav li.active {
  /*background-color: #222222;*/
  color: red;
}

.nav li:hover {
  /*background-color: #141414;*/
  color: rgb(141, 48, 48);
}

.welcome-title {
  color: white;
  /*display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;*/
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.enter-button {
  background-color: rgba(255, 0, 0, 0.411);
  text-decoration: none;
  color: inherit;
  padding: 0.3rem;
  position: fixed;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.splash {
  background-image: url("../bokeh.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.projects {
  color: white;
  /*position: fixed;
  list-style-type: none;
  border-top: 3px solid;
  border-bottom: 3px solid;
  padding-right: 90%;
  padding-left: 5%;
  margin-right: 90%;*/
  /*overflow: scroll;*/
}


.project-container{
  padding: 40px;
  border-color: #300e0e;
  border-style: solid;
  border-top: 3px solid;
  border-bottom: 3px solid;
}

.archives {
  color: white;
  display: block;
  align-items: center;
}

.newest-project {
  background-color: rgb(44, 0, 0);
  color: white;
  display: flex;
  align-items: center;
  gap: 10%;
  margin: 50px;
  margin-bottom: 20px;
  border: solid;
  border: #000000;
  border: 30px;
}
